import React from 'react';
import { graphql, PageProps } from 'gatsby';

import Box from '@mui/joy/Box';
import AppContainer from '../layouts/AppContainer';

//@ts-ignore
import friends from '../../content/friends.yaml';

export default function FriendsPage({ data }: PageProps<Queries.FriendsPageQuery>) {
  return (
    <AppContainer>
      <Box>
        {Object.entries(friends).map(([friendName, friend]) => {
          return (
            <Box>
              <h1>{friendName}</h1>
              {JSON.stringify(friend)}
            </Box>
          );
        })}
      </Box>
    </AppContainer>
  );
}

export const query = graphql`
  query FriendsPage {
    site {
      siteMetadata {
        title
      }
    }
  }
`;
